import styled from 'styled-components'


export const Container = styled.main`
    min-height: calc(100vh - 100px);
    padding: 0 calc(3.5vw + 5px);
    position: relative;
    overflow: hidden;
    &:before{
        background: ${props => ` url(${props.bg}) center center / cover no-repeat fixed`} ;
        content: "";
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        z-index: -1
    }

`